import styled from 'styled-components'

export const StyledSelect = styled.select`
border: none;
    padding: 5px;
    margin: 5px;
    margin-bottom: 20px;
    background-color: white;
    border-bottom: 1px solid black;
    width: 35%;
    &.select--short{
        width: 100%;
    }
`

export default StyledSelect
